body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

h1 {
	color: green;
	font-style: italic;
}

.note {
	color: grey;
	padding-top: 3px;
	font-style: 15px;
}

.error {
	color: red;
	background: lightgrey;
	font-size: 20px;
	border-style: solid;
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 10px;
}

.success {
	color: green;
	background: lightgrey;
	font-size: 20px;
	border-style: solid;
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 10px;
}
